import {
    actionModal
} from "./modal";


function post(trigger, modal, modalThanks, overlay, menu, container, body) {
    // $(trigger).submit(function (e) {
    //     e.preventDefault();
    //     $.ajax({
    //         type: "POST",
    //         url: "mailer/smart.php",
    //         data: $(this).serialize()
    //     }).done(function () {
    //         $(this).find("input").val("");
    //         actionModal('close', modal, overlay, menu, container, body);
    //         actionModal('open', modalThanks, overlay, menu, container, body);

    //         setTimeout(() => {
    //             actionModal('close', modalThanks, overlay, menu, container, body);
    //         }, 5000);

    //         $(trigger).trigger('reset');
    //     });
    //     return false;
    // });

    const forms = document.querySelectorAll(trigger);
    forms.forEach(form => {
        form.addEventListener('submit', function (e) {
            e.preventDefault();

            fetch('mailer/smart.php', {
                    method: 'POST',
                    body: new FormData(this)
                })
                .then(function (response) {
                    if (response.ok) {
                        form.querySelectorAll('input').forEach(input => {
                            input.value = '';
                        });
                        actionModal('close', modal, overlay, menu, container, body);
                        actionModal('open', modalThanks, overlay, menu, container, body);

                        setTimeout(function () {
                            actionModal('close', modalThanks, overlay, menu, container, body);
                        }, 5000);

                        form.reset();
                    }
                })
                .catch(error => {
                    console.log('Error:', error);
                });

            return false;
        });
    })
}

export default post;